import React, { Component } from 'react';
import { Link } from "react-router-dom";
import styles from '../Blog.module.css'

class BlogPage extends Component {
  render() {
    console.log(styles);
    const { data, styleName, prev, next } = this.props;
    return (
      <div className={styleName}>
        <h3>{data.title}</h3>
        <p>{data.content}</p>

        <p>&ensp;</p>
        {prev !== null && <Link to={`/blog/${prev.title}`} className={styles.link}>Previous article</Link>}
        {next !== null && <Link to={`/blog/${next.title}`} style={{ float: 'right', }} className={styles.link}>Next article </Link>}
      </div>
    )
  }
}

export default BlogPage;